// ----------------------------------------
// breakpoint
// ----------------------------------------

$num-breakpoint-sm: 320; // 320px
$num-breakpoint-md: 768; // 640px
$num-breakpoint-lg: 960; // 960px
$num-breakpoint-xl: 1312; // 1280px

$breakpoint-sm: #{$num-breakpoint-sm}px; // 320px
$breakpoint-md: #{$num-breakpoint-md}px; // 640px
$breakpoint-lg: #{$num-breakpoint-lg}px; // 960px
$breakpoint-xl: #{$num-breakpoint-xl}px; // 1280px;

// ----------------------------------------
// base
// ----------------------------------------

$gothic:"Yu Gothic", YuGothic, 'Noto Sans JP', -apple-system, BlinkMacSystemFont, "Helvetica Neue", " ProN W3", Hiragino Kaku Gothic ProN, Arial, "", Meiryo, sans-serif;
$mincho:"", YuMincho, "Hiragino Mincho ProN W3", " ProN W3", "Hiragino Mincho ProN", "HGE", " ", " ", serif;
$marugo:" Pro W4"," Pro","Hiragino Maru Gothic Pro"," Pro W3","Hiragino Kaku Gothic Pro","HGM-PRO","HGMaruGothicMPRO";
$lato: 'Lato', sans-serif;;
// ----------------------------------------
// num
// ----------------------------------------

// vw
$num-text-size: 4.268; //  4 ( => 4vw)
$num-viewport-padding: 0; //  0 ( => 0)
$num-layout-padding: 4.268; //  4 ( => 4vw)

// px
$num-text-root-wide: 16; //  16

// 
$num-text-small: 0.875;
$num-text-smaller: 0.75; //  0.75 => 0.75em
$num-text-smallest: 0.625;
$num-text-large: 1.125;
$num-text-larger: 1.25; //  1.25 => 1.25em
$num-text-largest: 1.375;
$num-text-ultralarge: 1.5;
$num-text-extralarge: 1.75;
$num-text-superlarge: 2;

// 
$num-line-root: 1.5; //  1.5
$num-line-narrow: 1.3; //  1.2
$num-line-wide: 1.7; //  1.2
$num-line-wider: 2; //  1.2
$num-line-one: 1; //  1 => 1rem
$num-contents-gap: 1.5; //  1.5

// 
$num-heading-document-number: 15.7; // 
$num-heading-part-number: 19.17; // 
$num-heading-chapter-number: 23.4375; // 
$num-heading-section-number: 16; // 
$num-heading-subsection-number: 18; // 
$num-heading-subsubsection-number: 20; // 
$num-text-number: 23;

// 
$num-line-title: $num-line-narrow; //  1.2 => 1.2rem
$num-line-text: $num-line-root; //  1.5 => 1.5rem
$num-viewport-layout-padding: $num-viewport-padding + $num-layout-padding; //  4 + 0 = 4 (=> 4vw)
$num-viewport-layout-paddings: $num-viewport-layout-padding * 2; //  4 * 2 = 8 (=> 8vw)
$num-layout-width: 100 - $num-viewport-layout-paddings; //  100 - 8 = 92 (=> 92vw)
$num-text-per-padding: $num-viewport-layout-padding / $num-text-size; //  4 / 4 = 1 (=> 1rem)
$num-text-per-paddings: $num-viewport-layout-paddings / $num-text-size; //  8 / 4 = 2 (=> 2rem)
$num-text-per-viewport-layout: 100 / $num-text-size; //  100 / 4 = 25 (=> 25rem)
$num-text-per-layout: $num-text-per-viewport-layout - $num-text-per-paddings; //  25 - 2 = 23 (=> 23rem)

// ----------------------------------------
// size
// ----------------------------------------

// px
$vp: 1rem / $num-text-root-wide;

// 
$size-layout-basis: $num-text-per-layout * 1rem; //  92 / 4 * 1rem = 23rem
$size-layout-padding: $num-text-per-padding * 1rem; //  1rem

// html
$size-text-root-narrow: $num-text-size * 1vw; // 4vw
$size-text-root-middle: 100vw / 1312 * $num-text-root-wide; // 4vw
$size-text-root-wide: $num-text-root-wide * 1px; // 16px
$size-text-small: 16 * $vp * $num-text-small;
$size-text-smaller: 16 * $vp * $num-text-smaller; //  0.75 => 0.75em
$size-text-smallest: 16 * $vp * $num-text-smallest;
$size-text-large: 16 * $vp * $num-text-large;
$size-text-larger: 16 * $vp * $num-text-larger; //  1.25 => 1.25em
$size-text-largest: 16 * $vp * $num-text-largest;
$size-text-ultralarge: 16 * $vp * $num-text-ultralarge;
$size-text-extralarge: 16 * $vp * $num-text-extralarge;
$size-text-superlarge: 16 * $vp * $num-text-superlarge;

// body =  / 
$size-text-main: $size-layout-basis / $num-text-per-layout; // = 1rem

// ----------------------------------------
// color
// ----------------------------------------

$color-black: #000;
$color-lightgray: #efefef;
$color-gray: #aaa;
$color-darkgray: #555;
$color-red: #bb272d;
$color-blue: #103889;
$color-green: #008c45;
$color-orange: #FBCA24;